<script>
import profile from '@/assets/images/profile.jpeg'
export default {
	setup() {
		return {
      profile,
			bios: [
				{
					id: 1,
					bio:
						'Mijn naam is Jaimy Putter. Ik ben geboren en getogen in zaandam. Sinds jongs af aan was ik al gefascineerd door technische snufjes. Wanneer er bijvoorbeeld een voorstelling plaatsvond stond ik onder het podium te kijken hoe alle apparatuur was aangesloten in plaats van dat ik van de voorstelling genoot. Ook ben ik vanaf mijn 12e begonnen met programmeren en sindsdien nooit gestopt.',
				},
				{
					id: 2,
					bio:
						'In mijn vrije tijd werk ik vaak aan projecten. Ik houd ervan mezelf uit te dagen en te variëren tussen projecten, aangezien ik zo elke keer wat nieuws leer. Zo vind ik het leuk om met hardware te bouwen (zoals een Raspberry Pi of server), maar ook games, websites & overige software te schrijven.',
				},
        {
          id: 3,
          bio:
              'Waar ik persoonlijk enorm trots op bent is mijn doorzettingsvermogen. Het maakt niet uit waar ik aan werk, als ik iets wilt dan stop ik niet voordat ik het voor elkaar hebt. Dit zie ik goed terug in mijn dagelijks leven, aangezien ik nieuwe vaardigheden & kennis enorm snel oppak.',
        },
        {
          id: 4,
          bio:
              'Verder vind ik het leuk om in m’n vrije tijd lekker actief te zijn. Zo doe ik aan fitness, wakeboarden en freerunning. Daarnaast vind ik het ook heerlijk om af en toe te gamen.'
        }
			],
		};
	},
};
</script>

<template>
  <div class="block sm:flex sm:gap-10 mt-10 sm:mt-20">
    <!-- About profile image -->
    <div class="w-full sm:w-1/4 mb-7 sm:mb-0">
      <img
        :src="profile"
        class="rounded-xl w-96"
        alt=""
      >
    </div>

    <!-- About details -->
    <div class="w-full sm:w-3/4 text-left">
      <p
        v-for="bio in bios"
        :key="bio.id"
        class="font-general-regular mb-4 text-ternary-dark dark:text-ternary-light text-lg"
      >
        {{ bio.bio }}
      </p>
    </div>
  </div>
</template>
